import { Button } from 'app/form-builder/form-components/button/styled';
import React, { useEffect, useState } from 'react';
import { InputWrapper, Input, QuestionTitle } from './styled';

const InputText = ({ placeholder, register, questionId, onNextStep, required, title }) => {
  const [value, setValue] = useState('');
  const [qtitle, setqtitle] = useState(title)

  const handleChange = (event) => {
    setValue(event.target.value);
  }

  useEffect(() => {
    setqtitle(title)
    setValue('')
  },[title]);

  const handleOk = () => {
    console.log(required)
    if (required) {
      if (value)
      {
        console.log(value)
        onNextStep()
      }else {
        console.log(qtitle.includes("required"))
        if (!qtitle.includes("required")) {
          const etitle = qtitle.replace("*", "required *")
          setqtitle(etitle)
        }
      }
    } else {
      onNextStep()
    }
  }

  return (
    <>
      <QuestionTitle style={{ color: qtitle.includes("required") ? "red": "black" }}>{qtitle}</QuestionTitle>
      <InputWrapper>
        <Input
          value={value}
          {...register(questionId.toString())}
          name={questionId.toString()}
          onChange={handleChange}
          placeholder={placeholder}
          style={{ color: "black" }}
        />
        <Button style={{ marginTop: 40, width: 155, height: 45, color: "white", backgroundColor: "#ff858a" }} onClick={handleOk}> OK </Button>
      </InputWrapper>
    </>
  );
}

export default InputText;